@use "global" as *;
.p-footer{
padding: rem(70) 0 rem(28);
background-color: $black2;
padding-left: $padding-pc;
padding-right: $padding-pc;
@include mq(){
    padding-left: $padding-sp;
    padding-right: $padding-sp;
    
    }
}
.p-footer__inner{
width: 100%;
max-width: rem(1400);
margin: 0 auto;
}
.p-footer__wrapper{
display: flex;
justify-content: space-between;
@include mq(md){
    flex-direction: column;
    text-align: center;
    
    }
}
.p-footer__logo{
 width: rem(264);
 height: rem(48);
 @include mq(md){
     margin: 0 auto;
     margin-bottom: rem(50);
     }

}
.p-footer__pc-nav{

}
.p-footer__pc-nav-items{
display: flex;
@include mq(md){
    flex-direction: column;
    }
}
.p-footer__pc-nav-item{
    font-size: rem(15);
    font-family: $font-family-base;
    margin-right: rem(20);
    font-weight: $bold;
    padding-top: rem(3);
    transition: all .5s;
    color: $white;
    @include mq(md){
        padding-bottom: rem(10);
        }
}
.p-footer__pc-nav-item:hover{
    opacity: .5;
}
.p-footer__copy-rights{
    text-align: center;
}
.p-footer__copy-right{
display: inline-block;
font-family: $font-family-english;
color: $white;
font-size: rem(16);
margin-top: rem(50);
}